module.exports = "## Line\r\n\r\n线状图。\r\n\r\n## 使用\r\n\r\n```html\r\n<m-line id=\"myLine\" width=\"600\" height=\"300\" \r\n  data=\"{\r\n    labels: ['Red', 'Blue', 'Yellow', 'Green', 'Purple', 'Orange'],\r\n    datasets: [{\r\n      label: '# of Votes',\r\n      data: [12, 19, 3, 5, 2, 3],\r\n      backgroundColor: 'rgba(255, 99, 132, .4)',\r\n      borderColor: 'rgb(255, 99, 132)',\r\n      borderWidth: 1\r\n    }]\r\n  }\"\r\n\r\n  options=\"{\r\n    responsive: true,\r\n    title: {\r\n      display: true,\r\n      text: 'Omi-chart Line Chart'\r\n    },\r\n    tooltips: {\r\n      mode: 'index',\r\n      intersect: false\r\n    },\r\n    hover: {\r\n      mode: 'nearest',\r\n      intersect: true\r\n    },\r\n    scales: {\r\n      xAxes: [{\r\n        display: true,\r\n        scaleLabel: {\r\n          display: true,\r\n          labelString: 'Month'\r\n        }\r\n      }],\r\n      yAxes: [{\r\n        display: true,\r\n        scaleLabel: {\r\n          display: true,\r\n          labelString: 'Value'\r\n        }\r\n      }]\r\n    }\r\n  }\">\r\n</m-line>\r\n```\r\n\r\n## 更新图表\r\n\r\n```js\r\n//获取该元素\r\nvar chart = document.querySelector('#myLine')\r\n//更改图表的数据\r\nchart.props.data.datasets.forEach(dataset => {\r\n  dataset.data.forEach((item, index) => {\r\n    dataset.data[index] = Math.random() * 100\r\n  })\r\n})\r\n//更新图表视图\r\nchart.update()\r\n```\r\n\r\n## API\r\n\r\n### Props\r\n\r\n```jsx\r\n{\r\n  data: object,\r\n  options: object,\r\n  width: number,\r\n  height: number,\r\n  horizontal?: string\r\n}\r\n```"

